var permissionSettings = {
  title: '',
  path: 'permission',
  icon: 'family',
  children: [
    {
      title: '',
      path: '/main/user',
      icon: 'facility'
    },
    {
      title: '',
      path: '/main/role',
      icon: 'facility'
    },
    {
      title: '',
      path: '/main/updatePassword',
      icon: 'facility'
    },
    {
      title: '',
      path: '/main/resetPassword',
      icon: 'facility',
      needAdmin: true
    }
  ]
}

export default permissionSettings



